.v-modal-enter {
  animation: v-modal-in 0.2s ease; }

.v-modal-leave {
  animation: v-modal-out 0.2s ease forwards; }

@keyframes v-modal-in {
  0% {
    opacity: 0; }
  100% { } }

@keyframes v-modal-out {
  0% { }
  100% {
    opacity: 0; } }

.v-modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  background: #000000; }

.tu-popup-parent--hidden {
  overflow: hidden; }

.tu-modal {
  position: relative;
  margin: 0 auto 50px;
  border-radius: 2px;
  box-sizing: border-box;
  color: #8394be;
  width: 50%; }
  .tu-modal.is-fullscreen {
    width: 100%;
    margin-top: 0;
    margin-bottom: 0;
    height: 100%;
    overflow: auto; }
  .tu-modal__wrapper {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: auto;
    margin: 0; }
  .tu-modal__header {
    padding: 12px 18px;
    font-size: 14px;
    line-height: 22px;
    border-bottom: 1px solid rgba(190, 200, 228, 0.38);
    vertical-align: middle; }
  .tu-modal__headerbtn {
    position: absolute;
    top: 0;
    right: 0;
    height: 49px;
    width: 49px;
    text-align: center;
    line-height: 49px;
    background: transparent;
    border: none;
    outline: none;
    margin: 0;
    padding: 0;
    cursor: pointer;
    font-size: 16px; }
    .tu-modal__headerbtn .tu-modal__close {
      color: #8394be;
      transition: color 0.3s; }
    .tu-modal__headerbtn:focus .tu-modal__close, .tu-modal__headerbtn:hover .tu-modal__close {
      color: #6d5dfc; }
  .tu-modal__title {
    line-height: 24px;
    font-size: 14px;
    color: #8394be;
    font-weight: 600; }
  .tu-modal__body {
    padding: 24px 18px;
    color: #8394be;
    font-size: 14px;
    word-break: break-all; }
  .tu-modal__footer {
    padding: 12px 18px;
    text-align: right;
    box-sizing: border-box;
    border-top: 1px solid rgba(190, 200, 228, 0.38); }
  .tu-modal--center {
    text-align: center; }
    .tu-modal--center .tu-modal__body {
      text-align: initial;
      padding: 12px 18px; }
    .tu-modal--center .tu-modal__footer {
      text-align: inherit; }

.modal-fade-enter-active {
  animation: modal-fade-in 0.3s; }

.modal-fade-leave-active {
  animation: modal-fade-out 0.3s; }

@keyframes modal-fade-in {
  0% {
    transform: translate3d(0, -20px, 0);
    opacity: 0; }
  100% {
    transform: translate3d(0, 0, 0);
    opacity: 1; } }

@keyframes modal-fade-out {
  0% {
    transform: translate3d(0, 0, 0);
    opacity: 1; }
  100% {
    transform: translate3d(0, -20px, 0);
    opacity: 0; } }
